.comics {
    &__list {
        margin-top: 45px;
        .button {
            margin: 0 auto;
            margin-top: 45px;
        }
    }
    &__item {
        transition: all 0.3s;
        &:hover {
            transform: translateY(-8px);
        }
        
        &-enter {
            opacity: 0;
        }
        &-enter-active {
            opacity: 1;
            transition: opacity 600ms ease-in;
        }
        &-exit {
            opacity: 1;
        }
        &-exit-active {
            opacity: 0;
            transition: opacity 600ms ease-in;
        }
    }
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(4, 225px);
        row-gap: 55px;
        justify-content: space-between;
    }
    
    &__img {
        width: 225px;
        height: 346px;
        object-fit: cover;
    }
    &__descr {
        color: #000;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
    }

    &__price {
        color: rgba(0, 0, 0, 0.60);
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        margin-top: 5px;
        text-transform: uppercase;
    }
}